<template>
  <el-dialog
    width="33%"
    :title="textMap[formType]"
    :visible="formVisible"
    :before-close="exitForm"
    :destroy-on-close="true"
    :close-on-click-modal="false"
  >

    <el-form
      ref="dataForm"
      class="wrapper"
      :rules="rules"
      label-position="top"
    >
      <el-form-item label="Upload data" prop="">
        <input type="file" @change="onFileChange">
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button class="cancel" size="medium" @click="formVisible = false">CANCEL</el-button>
      <el-button size="medium" @click="onSubmit">SUBMIT</el-button>
    </span>
  </el-dialog>

</template>

<script>
import readXlsxFile from 'read-excel-file'
import {
  importData
} from '@/graphql/unified'
export default {
  name: 'CallOutForm',
  props: [
    'formVisible',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    const client = process.env.VUE_APP_CLIENT
    return {
      client: client,
      loading: true,
      titleForm: 'User',
      textMap: {
        add: '',
        edit: ''
      },
      currentUser: JSON.parse(localStorage.getItem('user')),
      query: {
        module: '',
        data: '',
        user_registration_type: '',
        request: ''
      },
      rules: {},
      showField: true,
      fileList: [],
      fileTypes: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      fileUploadName: null,
      displayFileName: '',
      file: {},
      collectionData: '',
      collectionTable: '',
      acadYear: ''
    }
  },
  watch: {
    formVisible: function() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        }
      }
    }
  },
  mounted() {
    this.textMap.add = `Import Record`
    this.textMap.edit = `Edit ${this.titleForm}`
  },
  methods: {
    handleCreate() {},
    handleRemove() {
      this.file = null
      this.fileUploadName = null
    },
    onFileChange(e) {
      this.collectionTable = []
      var file = e.target.files ? e.target.files[0] : null
      readXlsxFile(file).then((data) => {
        console.log('data', data)
        data.forEach((item, index) => {
          if (index === 2) {
            this.acadYear = item[1]
          }
          if (index >= 6) {
            if (item[3] && item[4]) {
              this.collectionTable = this.collectionTable.concat({
                'id': undefined,
                'academic_year': this.acadYear,
                'client': this.client,
                'user_type': 'Student',
                'user_id': 0,
                'photo': '',
                'parent_info_id': 0,
                'current_address': '',
                'mailing_address': '',
                'academic_status': 'O',
                'sequence_number': item[0],
                'learners_reference_no': item[1],
                'student_number': item[2],
                'last_name': item[3],
                'first_name': item[4],
                'suffix': item[5],
                'middle_name': item[6],
                'gender': item[7],
                'dob': item[8],
                'complete_program_name': item[9],
                'contact_number': item[10],
                'acad_level': item[11],
                'father_last_name': item[12],
                'father_first_name': item[13],
                'father_middle_name': item[14],
                'mother_last_name': item[15],
                'mother_first_name': item[16],
                'mother_middle_name': item[17],
                'dswd_household_no': item[18],
                'household_per_capital_income': item[19],
                'permanent_address_street_barangay': item[20],
                'permanent_address_town_city_mun': item[21],
                'permanent_address_province': item[22],
                'permanent_address_zipcode': item[23],
                'total_units': item[24],
                'total_assessment': item[25],
                'disability': item[26],
                'applicant_status': item[27],
                'remarks': item[28],
                'age': ''
              })
            }
          }
        })
      })
    },
    onSubmit() {
      importData({
        data: this.collectionTable
      }, (response, success) => {
        if (success) {
          if (response.importData.success) {
            this.$message({
              message: response.importData.message,
              dangerouslyUseHTMLString: true,
              type: 'success'
            })
            this.$emit('formVisible')
            this.$emit('formSuccess')
          } else {
            this.$emit('formVisible')
            this.$emit('formSuccess')
          }
        } else {
          this.$message({
            message: 'Email duplicate found, please update names',
            dangerouslyUseHTMLString: true,
            type: 'error'
          })
        }
      })
    },
    exitForm() {
      this.formVisible = false
    }
  }
}
</script>

<style lang="scss" scoped>
.cancel {
  background-color: $cancel;
  color: gray;

}
</style>
