<script>
import {
  unifiedQuery
} from '@/graphql/unified'

export default {
  name: 'Dashboard',
  data() {
    // const clientIdentifier = process.env.VUE_APP_CLIENT
    return {
      currentUser: JSON.parse(localStorage.getItem('user')),
      query: {
        first: 10,
        page: 1,
        module: '',
        search: '',
        user_id: '',
        user_registration_type: ''
      },
      result: []
    }
  },
  beforeMount() {
    if (this.currentUser.registration_type !== 'Administrator') {
      window.location.reload()
    }
  },
  mounted() {
    this.query.module = this.$route.name
    this.query.user_registration_type = this.currentUser.registration_type
    console.log('query', this.query)
    this.get()
  },
  methods: {
    get() {
      unifiedQuery(this.query, (response, success) => {
        if (success) {
          const data = response.unifiedQuery.data
          if (data) {
            this.result = data
          }
          console.log('data', data)
          console.log('this.result', this.result)
        }
      })
    }
  }
}
</script>

