        </div></div>
        <div class="Grid-cell"><div class="Demo content-1of3">
          <el-form-item label="No children" prop="no_children">
            <el-input type="number" v-model="dataForm.no_children" placeholder="No children" />
          </el-form-item>
        </div></div>
      </div>

      <div class="Grid Grid--gutters Grid--cols-2 u-textLeft">
        <div class="Grid-cell"><div class="Demo content-1of2">
          <el-form-item label="Nationality" prop="nationality">
            <el-radio-group v-model="dataForm.nationality">
              <el-radio label="Filipino" /><br>
              <el-radio label="Other" /><br>
            </el-radio-group>
          </el-form-item>
        </div></div>
        <div class="Grid-cell"><div class="Demo content-1of2">
          <el-form-item label="Educational Background" prop="educational_background_id">
            <el-radio-group v-model="dataForm.educational_background_id">
              <el-radio label="Elementary" /><br>
              <el-radio label="High School" /><br>
              <el-radio label="Vocational College" /><br>
              <el-radio label="Post-Graduate" /><br>
              <el-radio label="Other" /><br>
            </el-radio-group>
          </el-form-item>
        </div></div>
      </div>

      <div class="Grid Grid--gutters Grid--cols-3 u-textLeft">
        <div class="Grid-cell"><div class="Demo content-1of3">
          <el-form-item label="Person to notify in case of emergency" prop="contact_person">
            <el-input v-model="dataForm.contact_person" placeholder="Person to notify in case of emergency" />
          </el-form-item>
        </div></div>
        <div class="Grid-cell"><div class="Demo content-1of3">
          <el-form-item label="Relationship" prop="contact_person_relationship">
            <el-input v-model="dataForm.contact_person_relationship" placeholder="Relationship" />
          </el-form-item>
        </div></div>
        <div class="Grid-cell"><div class="Demo content-1of3">
          <el-form-item label="Contact No" prop="contact_person_contact_number">
            <el-input v-model="dataForm.contact_person_contact_number" placeholder="Contact No" />
          </el-form-item>
        </div></div>
      </div>

      <el-form-item label="Address" prop="contact_person_address">
        <el-input v-model="dataForm.contact_person_address" placeholder="Address" />
      </el-form-item>

      <h2 class="labelSizeInForm">Organization</h2>
      <el-divider />
      <table class="table table-hover table-striped table-dark w-100">
        <thead>
          <tr class="table-active">
            <th>Name of organization</th>
            <th>Member since</th>
            <th>Designation</th>
            <th>Delete</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in dataForm.organizationTableData" :key="item">
            <td><el-input v-model="item.org_name" type="text" class="form-control groupName" value="item.groupName" /></td>
            <td><el-input v-model="item.member_since" type="text" class="form-control" value="item.equipmentName" /></td>
            <td><el-input v-model="item.designation" type="text" class="form-control" value="item.description" /></td>
            <td><el-button class="btn btn-danger" @click="deleteGroup(index)"> X </el-button> </td>
          </tr>
        </tbody>
      </table>
      <el-button class="btn btn-info float-right" @click="addGroup">Add</el-button><br>
      <div v-if="formType === formTypes.edit">
        <h3>Requirements</h3>
        <el-table
          :header-cell-style="{ background: '#7c96a6', color: 'white' }"
          style="width: 100%"
          stripe
          border
          fit
          :data="requirements"
          height="500"
        >
          <el-table-column
            prop="requirement"
            label="Requirement"
            width="auto"
          >
            <template slot-scope="{row}">
              <div>{{ row.requirement }}</div>
              <el-link v-if="row.status !== 'O'" type="text" @click="viewImage(row)">view image</el-link>
            </template>
          </el-table-column>
          <el-table-column
            prop="status"
            label="Status"
            width="auto"
          >
            <template slot-scope="{row}">
              <div v-if="row.status !== 'O'" style="text-align: left">
                <el-tag v-if="row.status === 'U'" stype="success">Uploaded</el-tag>
                <el-tag v-else-if="row.status === 'A'">Approved</el-tag>
                <el-tag v-else-if="row.status === 'D'" type="danger">Denied</el-tag>
                <el-tag v-else style="width: 100%" stype="success">Open</el-tag>
                <div class="file-upload-form">
                  <label class="custom-file-upload" style="color: white; padding: 2%">
                    <input type="file" accept="image/*" @change="submitUpload(row)">
                    click here to update file
                  </label>
                </div>
              </div>
              <div v-else>
                <div class="file-upload-form">
                  <label class="custom-file-upload" style="color: white; padding: 2%">
                    <input type="file" accept="image/*" @change="submitUpload(row)">
                    Upload Profile
                  </label>
                </div>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <br>
      <h2 class="labelSizeInForm">Certificate</h2>
      <el-divider />
      <div style="width: 100%">
        <el-checkbox v-model="certify" />
        I have personally reviewed the information on this application and I certify under penalty of perjury that to the best of my knowledge and behalf the information on this application is true and correct, and that I understand this information is subject to public disclosure
      </div>
      <el-dialog
        :title="titleRequirement"
        :visible.sync="centerDialogVisible"
        width="90%"
        center
      >
        <el-image
          style="width: 100%; height: 100%"
          :src="ImageUrl"
        />
      </el-dialog>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button class="cancel" size="medium" @click="exitForm">CANCEL</el-button>
      <el-button size="medium" @click="onSubmit">SAVE</el-button>
    </span>
  </el-dialog>

</template>

<script>
import {
  mutationFishery
} from '@/graphql/fishery'
import {
  uploadFile
} from '@/graphql/users'
export default {
  name: 'CallOutForm',
  props: [
    'formVisible',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    return {
      currentUser: JSON.parse(localStorage.getItem('user')),
      loading: false,
      titleForm: 'Application For Municipal FisherFolks Registration',
      textMap: {
        add: '',
        edit: ''
      },
      query: {
        user_id: '',
        user_type: '',
        module_type: this.$route.name,
        request: '',
        data: ''
      },
      dataForm: {
        id: undefined,
        photo: '',
        fishery_other_info_id: '',
        user_title: '',
        last_name: '',
        first_name: '',
        middle_name: '',
        suffix: '',
        address: '',
        barangay: '',
        contact_info_id: '',
        residence_year: '',
        age: '',
        dob: '',
        pob: '',
        livelihood: '',
        gender: '',
        civil_status: '',
        no_children: '',
        nationality: '',
        educational_background_id: '',
        contact_person: '',
        contact_person_relationship: '',
        contact_person_contact_number: '',
        contact_person_address: '',
        organizationTableData: []
      },
      rules: {
        fishery_other_info_id: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        user_title: [{ required: true, message: 'This field is required.', trigger: 'change' }],
        last_name: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        first_name: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        middle_name: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        suffix: [{ required: false, message: 'This field is required.', trigger: 'blur' }],
        address: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        barangay: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        contact_info_id: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        residence_year: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        age: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        dob: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        pob: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        gender: [{ required: true, message: 'This field is required.', trigger: 'change' }],
        civil_status: [{ required: true, message: 'This field is required.', trigger: 'change' }],
        no_children: [{ required: true, message: 'This field is required.', trigger: 'change' }],
        nationality: [{ required: true, message: 'This field is required.', trigger: 'change' }],
        educational_background_id: [{ required: true, message: 'This field is required.', trigger: 'change' }],
        contact_person: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        contact_person_relationship: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        contact_person_contact_number: [{ required: true, message: 'This field is required.', trigger: 'blur' }],
        contact_person_address: [{ required: true, message: 'This field is required.', trigger: 'blur' }]
      },
      certify: false,
      titleRequirement: '',
      ImageUrl: '',
      centerDialogVisible: false,
      requirements: []
    }
  },
  watch: {
    formVisible: function() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        } else if (this.formType === this.formTypes.edit) {
          this.handleUpdate(this.formData)
        }
      }
    }
  },
  mounted() {
    console.log('query', this.query)
    this.textMap.add = `Add New ${this.titleForm}`
    this.textMap.edit = `Edit ${this.titleForm}`
  },
  methods: {
    viewImage(row) {
      console.log('row', row)
      this.centerDialogVisible = true
      this.ImageUrl = row.photo
      this.titleRequirement = row.requirement
    },
    submitUpload(row) {
      console.log('row', row)
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          row['photo'] = e.target.result
          row['status'] = 'U'
          const query = {
            data: row
          }
          console.log('query', query)
          uploadFile(query, (response, success) => {
            if (success) {
              console.log('uploadFile response', response)
              this.$message({
                message: response.uploadFile.message,
                dangerouslyUseHTMLString: true,
                type: 'success'
              })
            } else {
              this.$message({
                message: response.uploadFile.message,
                dangerouslyUseHTMLString: true,
                type: 'error'
              })
            }
          })
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    previewImage(event) {
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.dataForm.photo = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    handleCreate() {
      console.log('handleCreate')
      this.loading = false
    },
    handleUpdate(row) {
      console.log('handleUpdate', row)
      this.loading = false
      this.dataForm = row
      console.log('this.dataForm', this.dataForm)
      if (row) {
        row.user.requirements.forEach(rowItem => {
          if (rowItem.requirement !== 'Picture of boat with owner') {
            const requirement = this.requirements.find((item) => item.requirement === rowItem.requirement)
            if (requirement === undefined) {
              this.requirements.push(rowItem)
            }
          }
        })
      }
    },
    onSubmit() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.create()
        } else if (this.formType === this.formTypes.edit) {
          this.update()
        }
      }
    },
    create() {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          this.query.request = 'create'
          this.query.user_id = this.currentUser.id.toString()
          this.query.user_type = this.currentUser.registration_type
          this.dataForm.organizationTableData = JSON.stringify(this.organizationTableData)
          this.query.data = this.dataForm
          console.log('this.query', this.query)
          mutationFishery(this.query, (response, success) => {
            if (success) {
              const data = response.mutationFishery.data
              console.log('data', data)
              this.exitForm()
              this.successForm()
              this.$message({
                message: response.mutationFishery.message,
                dangerouslyUseHTMLString: true,
                type: 'success'
              })
            } else {
              this.$message({
                message: response.mutationFishery.message,
                dangerouslyUseHTMLString: true,
                type: 'error'
              })
            }
          })
        }
      })
    },
    addGroup() {
      this.dataForm.organizationTableData.push({
        org_name: '',
        member_since: '',
        designation: ''
      })
    },
    deleteGroup(index) {
      this.dataForm.organizationTableData.splice(index, 1)
    },
    update() {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          this.query.request = 'update'
          this.query.user_id = this.currentUser.id.toString()
          this.query.user_type = this.currentUser.registration_type
          this.dataForm.organizationTableData = JSON.stringify(this.organizationTableData)
          this.query.data = this.dataForm
          console.log('this.query', this.query)
          mutationFishery(this.query, (response, success) => {
            if (success) {
              const data = response.mutationFishery.data
              console.log('data', data)
              this.exitForm()
              this.successForm()
              this.$message({
                message: response.mutationFishery.message,
                dangerouslyUseHTMLString: true,
                type: 'success'
              })
            } else {
              this.$message({
                message: response.mutationFishery.message,
                dangerouslyUseHTMLString: true,
                type: 'error'
              })
            }
          })
        }
      })
    },
    resetModel() {
      this.dataForm = {
        id: undefined,
        photo: '',
        fishery_other_info_id: '',
        user_title: '',
        last_name: '',
        first_name: '',
        middle_name: '',
        suffix: '',
        address: '',
        barangay: '',
        contact_info_id: '',
        residence_year: '',
        age: '',
        dob: '',
        pob: '',
        livelihood: '',
        gender: '',
        civil_status: '',
        no_children: '',
        nationality: '',
        educational_background_id: '',
        contact_person: '',
        contact_person_relationship: '',
        contact_person_contact_number: '',
        contact_person_address: '',
        organizationTableData: []
      }
      this.formVisible = false
    },
    exitForm() {
      this.resetModel()
      this.$emit('formVisible')
    },
    successForm() {
      this.$emit('formSuccess')
    }
  }
}
</script>

<style lang="scss" scoped>
.cancel {
  background-color: $cancel;
  color: gray;
}
.file-upload-form, .image-preview {
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  padding: 20px;
}
img.preview {
  width: 200px;
  height: 200px;
  background-color: white;
  padding: 5px;
  border-radius: 2%;
}
</style>
