<template>
  <div class="dashboard-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <h3>{{ this.$route.name }}</h3>
        <el-dropdown style="margin-left: 1%; float: right;" @command="exportData">
          <el-button
            size="medium"
            class="filter-item button-primary actionSizes"
            icon="el-icon-download"
            :loading="printLoading"
          >
            Export as<i class="el-icon-arrow-down el-icon--right" />
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="xlsx">Excel</el-dropdown-item>
            <el-dropdown-item command="pdf">PDF</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!--        <el-button-->
        <!--          class="button-primary actionSizes"-->
        <!--          style="margin-left: 1%; float: right;"-->
        <!--          size="medium"-->
        <!--          icon="el-icon-plus"-->
        <!--          @click="addNewRecord"-->
        <!--        >-->
        <!--          Add New Record-->
        <!--        </el-button>-->
        <div style="float: right;" class="actionSearchSizes">
          <el-input v-model="searchValue" size="medium" placeholder="search here" class="input-with-select" @keyup.enter.native="handleSearch">
            <el-button
              slot="append"
              style="background-color: #FFA500; color: white"
              icon="el-icon-search"
              class="btn-primary-color"
              @click="handleSearch"
            >Search
            </el-button>
          </el-input>
        </div>
      </div>
      <pagination
        v-show="total>0"
        :total="total"
        :page.sync="query.page"
        :limit.sync="query.first"
        @pagination="get"
      />
      <el-table
        v-loading="tableLoading"
        :header-cell-style="{ background: '#7c96a6', color: 'white' }"
        style="width: 100%"
        stripe
        border
        fit
        :data="userAccounts"
        height="650"
      >
        <el-table-column
          prop="id"
          type="index"
          label="#"
          :index="indexMethod"
          width="50"
          align="center"
        />
        <el-table-column
          label="Student Info"
          width="auto"
        >
          <template slot-scope="{row}">
            <div>Program: {{ row.complete_program_name }}</div>
            <div>Student ID: {{ row.student_number }}</div>
            <div>Learners reference no: {{ row.learners_reference_no }}</div>
            <div v-if="row.user">
              Name: {{ row.user.name }}
            </div>
            <div>Email: {{ row.email }}</div>
            <div>Academic Year: {{ row.academic_year }}</div>
            <div>Academic Level: {{ row.acad_level }}</div>
          </template>
        </el-table-column>
        <el-table-column
          label="TOTAL ASSESSMENT"
          width="auto"
          style="vertical-align: top"
        >
          <template slot-scope="{row}">
            <div>Total Units: {{ row.total_units }}</div>
            <div>Total Assessment: {{ row.total_assessment }}</div>
          </template>
        </el-table-column>
        <el-table-column
          label="Status"
          width="200"
        >
          <template slot-scope="{row}">
            <div style="text-align: center">
              <el-tag v-if="row.status === 'P'" stype="success">Approved</el-tag>
              <el-tag v-else-if="row.status === 'W'">Review</el-tag>
              <el-tag v-else-if="row.status === 'D'" type="danger">Denied</el-tag>
              <el-tag v-else style="width: 100%" stype="success">Open</el-tag>
            </div>
          </template>
        </el-table-column>
        <!--        <el-table-column-->
        <!--          label="Actions"-->
        <!--          width="150"-->
        <!--        >-->
        <!--          <template slot-scope="{row}">-->
        <!--            <span>-->
        <!--&lt;!&ndash;              <el-button class="btn-update" icon="el-icon-edit" circle @click="updateRecord(row)" />&ndash;&gt;-->
        <!--&lt;!&ndash;              <el-button class="btn-danger" icon="el-icon-delete" circle @click="openDeleteQuestion(row)" />&ndash;&gt;-->
        <!--            </span>-->
        <!--          </template>-->
        <!--        </el-table-column>-->
      </el-table>
      <div style="padding: 14px;">
        <pagination
          v-show="total>0"
          :total="total"
          :page.sync="query.page"
          :limit.sync="query.first"
          @pagination="get"
        />
      </div>
    </el-card>
    <call-out-form
      :form-visible="formVisible"
      :form-types="formTypes"
      :form-type="formType"
      :form-data="formData"
      @formSuccess="get"
    />
  </div>
</template>

<script>
import Pagination from '@/components/Pagination'
import CallOutForm from '@/views/unified/user-accounts/Form'
import {
  unifiedQuery,
  unifiedMutation
} from '@/graphql/unified'
import { exportExcel, exportPDF } from '@/utils/report'
export default {
  name: 'Dashboard',
  components: {
    Pagination,
    CallOutForm
  },
  data() {
    return {
      formTypes: {
        add: 'add',
        edit: 'edit'
      },
      formType: '',
      formData: {},
      formVisible: false,
      tableLoading: false,
      printLoading: false,
      searchValue: '',
      currentUser: JSON.parse(localStorage.getItem('user')),
      query: {
        first: 10,
        page: 1,
        module: '',
        search: '',
        user_id: '',
        user_registration_type: ''
      },
      mutationRequest: {
        module: '',
        data: '',
        user_registration_type: '',
        request: ''
      },
      total: 0,
      allData: [],
      userAccounts: []
    }
  },
  watch: {
    searchValue: function() {
      if (this.searchValue === '') {
        this.handleSearch()
      }
    }
  },
  mounted() {
    this.get()
  },
  methods: {
    get() {
      this.formVisible = false
      this.tableLoading = true
      this.userAccounts = []
      console.log('this.query', this.query)
      console.log('this.query', this.query)
      this.query.module = this.$route.name
      this.query.user_registration_type = this.currentUser.registration_type
      this.query.user_id = this.currentUser.id.toString()
      unifiedQuery(this.query, (response, success) => {
        if (success) {
          const data = response.unifiedQuery.data
          this.total = response.unifiedQuery.paginator_info.total
          if (data.length > 0) {
            this.userAccounts = data
            this.tableLoading = false
            this.getAll()
          } else {
            this.tableLoading = false
          }
        } else {
          this.tableLoading = false
          this.$message({
            message: `Unable to retrieve the data.`,
            dangerouslyUseHTMLString: true,
            type: 'error'
          })
        }
      })
    },
    handleSearch() {
      this.query.page = 1
      this.query.search = this.searchValue
      this.query.module = this.$route.name
      this.query.user_registration_type = this.currentUser.registration_type
      this.query.user_id = this.currentUser.id.toString()
      this.get()
    },
    indexMethod(index) {
      return (index + 1) + ((this.query.page - 1) * this.query.first)
    },
    openDeleteQuestion(data) {
      this.$confirm('This will permanently delete the record. Continue?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning',
        center: true
      }).then(() => {
        this.mutationRequest.module = this.$route.name
        this.mutationRequest.data = data
        this.mutationRequest.user_registration_type = this.currentUser.registration_type
        this.mutationRequest.request = 'delete'
        unifiedMutation(this.mutationRequest, (response, success) => {
          if (success) {
            this.get()
          } else {
            this.tableLoading = false
            this.$message({
              message: response.message,
              dangerouslyUseHTMLString: true,
              type: 'error'
            })
          }
        })
      })
    },
    addNewRecord() {
      this.formVisible = true
      this.formType = this.formTypes.add
      this.formData = {}
    },
    updateRecord(row) {
      this.formVisible = true
      this.formType = this.formTypes.edit
      this.formData = row
    },
    getAll() {
      this.query.first = this.total
      unifiedQuery(this.query, (response, success) => {
        if (success) {
          const data = response.unifiedQuery.data
          if (data.length > 0) {
            this.allData = data
            console.log('this.allData', this.allData)
          }
        }
      })
    },
    exportData(type) {
      let collectData = ''
      let columns = ''
      const propertiesPdf = [
        { field: 'index', displayName: '#', columnSize: 'auto' },
        { field: 'student', displayName: 'Student Info', columnSize: 'auto' },
        { field: 'total_assessment', displayName: 'Total assessment', columnSize: 'auto' }
      ]
      const propertiesExcel = [
        { field: 'index', displayName: '#', columnSize: 'auto' },
        { field: 'program', displayName: 'Program', columnSize: 'auto' },
        { field: 'student_id', displayName: 'Student ID', columnSize: 'auto' },
        { field: 'reference', displayName: 'Learners reference no', columnSize: 'auto' },
        { field: 'name', displayName: 'Name', columnSize: 'auto' },
        { field: 'email', displayName: 'Email', columnSize: 'auto' },
        { field: 'academic_year', displayName: 'Academic Year', columnSize: 'auto' },
        { field: 'academic_level', displayName: 'Academic Level', columnSize: 'auto' },
        { field: 'units', displayName: 'Total Units', columnSize: 'auto' },
        { field: 'assessment', displayName: 'Total Assessment', columnSize: 'auto' }
      ]

      const headerPdf = propertiesPdf.map(property => property.displayName)
      const headerExcel = propertiesExcel.map(property => property.displayName)

      if (type === 'pdf') {
        let pdfData = this.customizeDataPdf(this.allData)
        pdfData = pdfData.map(elem => Object.values(elem))
        exportPDF(this.$route.name, headerPdf, pdfData)
      } else {
        // excel
        collectData = this.customizeDataExcel(this.allData)
        columns = propertiesExcel.map(property => property.field)
        exportExcel(this.$route.name, headerExcel, columns, collectData, type)
      }
    },
    customizeDataPdf(programs) {
      let target = {}
      const data = []
      let counter = 1
      programs.forEach((element) => {
        console.log('element', element)
        target = {
          index: counter++,
          student: `Program: ${element.complete_program_name}\n` +
            `Student ID: ${element.student_number}\n` +
            `Learners reference no: ${element.learners_reference_no}\n` +
            `Name: ${element.user.name}\n` +
            `Email: ${element.user.email}\n` +
            `Academic Year: ${element.academic_year}\n` +
            `Academic Level: ${element.acad_level}`,
          total_assessment: `Total Units: ${element.total_units} \n` +
            `Total Assessment: ${element.total_assessment}`
        }
        data.push(Object.assign({}, target))
      })
      return data
    },
    customizeDataExcel(programs) {
      let target = {}
      const data = []
      let counter = 1
      programs.forEach((element) => {
        console.log('element', element)
        target = {
          index: counter++,
          program: `${element.complete_program_name}`,
          student_id: `${element.student_number}`,
          reference: `${element.learners_reference_no}`,
          name: `${element.user.name}`,
          email: `${element.user.email}`,
          academic_year: `${element.academic_year}`,
          academic_level: `${element.acad_level}`,
          units: `${element.total_units}`,
          assessment: `${element.total_assessment}`
        }
        data.push(Object.assign({}, target))
      })
      return data
    }
  }
}
</script>

