    <span slot="footer" class="dialog-footer">
      <el-button class="cancel" size="medium" @click="formVisibleNew = false">CANCEL</el-button>
      <el-button size="medium" @click="onSubmit">SAVE</el-button>
    </span>
  </el-dialog>

</template>

<script>
import {
  unifiedMutation
} from '@/graphql/unified'
export default {
  name: 'NewForm',
  props: [
    'formVisibleNew',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    const client = process.env.VUE_APP_CLIENT
    return {
      client: client,
      genderOptions: [
        { id: 1, value: 'Male', label: 'Male' },
        { id: 2, value: 'Female', label: 'Female' }
      ],
      granteesOptions: [
        { id: 1, value: 'grantees', label: 'Grantees' },
        { id: 2, value: 'on going', label: 'On going' },
        { id: 3, value: 'not enrolled', label: 'Not enrolled' },
        { id: 4, value: 'enrolled', label: 'Enrolled' },
        { id: 5, value: 'graduated', label: 'Graduated' }
      ],
      loading: true,
      titleForm: 'Record',
      textMap: {
        add: '',
        edit: ''
      },
      currentUser: JSON.parse(localStorage.getItem('user')),
      query: {
        module: '',
        data: '',
        user_registration_type: '',
        request: ''
      },
      dataForm: {
        id: undefined,
        academic_year: '',
        client: client,
        user_type: '',
        user_id: '',
        photo: '',
        parent_info_id: '',
        current_address: '',
        mailing_address: '',
        academic_status: '',
        sequence_number: '',
        learners_reference_no: '',
        student_number: '',
        last_name: '',
        first_name: '',
        suffix: '',
        middle_name: '',
        gender: '',
        dob: '',
        complete_program_name: '',
        contact_number: '',
        acad_level: '',
        father_last_name: '',
        father_first_name: '',
        father_middle_name: '',
        mother_last_name: '',
        mother_first_name: '',
        mother_middle_name: '',
        dswd_household_no: '',
        household_per_capital_income: '',
        permanent_address_street_barangay: '',
        permanent_address_town_city_mun: '',
        permanent_address_province: '',
        permanent_address_zipcode: '',
        total_units: '',
        total_assessment: '',
        disability: '',
        applicant_status: '',
        remarks: '',
        age: '',
        temp_password: ''
      },
      passwordType: 'password',
      rules: {
        last_name: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        first_name: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        middle_name: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        email: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        password: [{ required: true, message: 'This is required.', trigger: 'blur' }],
        password_confirmation: [{ required: true, message: 'This is required.', trigger: 'blur' }]
      },
      showField: true
    }
  },
  watch: {
    formVisibleNew: function() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        } else if (this.formType === this.formTypes.edit) {
          this.handleUpdate(this.formData)
        }
      }
    }
  },
  mounted() {
    this.textMap.add = `Add New ${this.titleForm}`
    this.textMap.edit = `Edit ${this.titleForm}`
  },
  methods: {
    handleCreate() {
      this.loading = false
      this.resetModel()
    },
    handleUpdate(row) {
      console.log('row', row)
      this.loading = false
      if (row['gender'] === 'M') {
        row['gender'] = 'Male'
      }
      if (row['gender'] === 'F') {
        row['gender'] = 'Female'
      }
      this.dataForm = row
      row['parent_infos'].forEach(item => {
        console.log('item[\'member\']', item['member'])
        if (item['member'] === 'father') {
          this.dataForm.father_last_name = item['last_name'] ?? ''
          this.dataForm.father_first_name = item['first_name'] ?? ''
          this.dataForm.father_middle_name = item['middle_name'] ?? ''
        } else {
          this.dataForm.mother_last_name = item['last_name'] ?? ''
          this.dataForm.mother_first_name = item['first_name'] ?? ''
          this.dataForm.mother_middle_name = item['middle_name'] ?? ''
        }
      })
    },
    onSubmit() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.create()
        } else if (this.formType === this.formTypes.edit) {
          this.update()
        }
      }
    },
    create() {
      this.query.module = this.$route.name
      this.query.user_registration_type = this.currentUser.registration_type
      this.query.request = 'create'
      this.query.data = this.dataForm
      unifiedMutation(this.query, (response, success) => {
        if (success) {
          this.$message({
            message: response.unifiedMutation.message,
            dangerouslyUseHTMLString: true,
            type: 'success'
          })
          this.exitForm()
          this.successForm()
        } else {
          this.tableLoading = false
          this.$message({
            message: response.message,
            dangerouslyUseHTMLString: true,
            type: 'error'
          })
        }
      })
    },
    update() {
      this.query.module = this.$route.name
      this.query.user_registration_type = this.currentUser.registration_type
      this.query.request = 'update'
      this.query.data = this.dataForm
      unifiedMutation(this.query, (response, success) => {
        if (success) {
          this.$message({
            message: response.unifiedMutation.message,
            dangerouslyUseHTMLString: true,
            type: 'success'
          })
          this.exitForm()
          this.successForm()
        } else {
          this.tableLoading = false
          this.$message({
            message: response.message,
            dangerouslyUseHTMLString: true,
            type: 'error'
          })
        }
      })
    },
    resetModel() {
      this.dataForm = {
        id: undefined,
        academic_year: '',
        client: this.client,
        user_type: '',
        user_id: '',
        photo: '',
        parent_info_id: '',
        current_address: '',
        mailing_address: '',
        academic_status: '',
        sequence_number: '',
        learners_reference_no: '',
        student_number: '',
        last_name: '',
        first_name: '',
        suffix: '',
        middle_name: '',
        gender: '',
        dob: '',
        complete_program_name: '',
        contact_number: '',
        acad_level: '',
        father_last_name: '',
        father_first_name: '',
        father_middle_name: '',
        mother_last_name: '',
        mother_first_name: '',
        mother_middle_name: '',
        dswd_household_no: '',
        household_per_capital_income: '',
        permanent_address_street_barangay: '',
        permanent_address_town_city_mun: '',
        permanent_address_province: '',
        permanent_address_zipcode: '',
        total_units: '',
        total_assessment: '',
        disability: '',
        applicant_status: '',
        remarks: '',
        age: '',
        temp_password: ''
      }
    },
    exitForm() {
      this.formVisibleNew = false
      this.resetModel()
      this.$emit('formVisible')
    },
    successForm() {
      this.$emit('formSuccess')
    }
  }
}
</script>

