import JSPDF from 'jspdf'
import 'jspdf-autotable'
import { Message } from 'element-ui'

export function printApplicationForm(url, applicationData) {
  const show = generateForm(url, applicationData)
  popupCenter(show.url, show.pdfName, 1024, 768)
}

function generateForm(url, applicationData, willReturnPDF = false) {
  const clientTemplate = process.env.VUE_APP_CLIENT

  const banner = require('@/assets/' + clientTemplate + '/banner.png')
  const logo = require('@/assets/' + clientTemplate + '/logo.png')
  const imgBanner = new Image()
  const imgLogo = new Image()
  imgBanner.src = banner
  imgLogo.src = logo

  const pdfName = url
  const doc = new JSPDF({
    orientation: 'portrait', // landscape
    font: 'helvetica',
    unit: 'mm',
    format: 'legal'
  })

  const headerColor = '#D4D4D4'
  const headerTextColor = [0, 0, 0]
  const additionalTopMargin = 0
  const tableHeaderFontSize = 8
  const cellPadd = 0.7
  doc.autoTable({
    title: pdfName,
    margin: { top: 50 + additionalTopMargin, left: 10, right: 10 },
    theme: 'grid',
    tableLineColor: [0, 0, 0],
    headStyles: {
      fillColor: headerColor,
      textColor: headerTextColor,
      halign: 'left',
      fontSize: tableHeaderFontSize,
      cellPadding: cellPadd,
      valign: 'middle'
    },
    columnStyles: {
      0: { halign: 'left', valign: 'left', cellPadding: cellPadd }
    },
    didDrawPage: function(data) {
      doc.addImage(imgBanner, 'PNG', data.settings.margin.left, 10, doc.internal.pageSize.width - 20, 30)
      doc.addImage(imgLogo, 'PNG', data.settings.margin.left, 10, 50, 30)
      doc.setFontSize(12)
      const textWidth = doc.getTextWidth('APPLICANT INFORMATION SHEET')
      const textOffset = (doc.internal.pageSize.width - textWidth) / 2
      doc.text('APPLICANT INFORMATION SHEET', textOffset, 50)
    }
  })

  let xPos = 50 + 10
  doc.setFontSize(12)
  // Empty square
  const imgData = applicationData.photo
  // data:image/jpeg;base64
  let profile = applicationData.photo.split(',')
  profile = profile[0].split('/')
  profile = profile[1].split(';')
  doc.addImage(imgData, profile[0], 10, xPos, xPos, xPos, undefined, 'FAST')
  doc.rect(10, xPos, xPos, xPos)

  doc.setFontStyle('normal')
  doc.text(`Registration No.: ${applicationData.fishery_other_info_id}`, 10, xPos + 70)
  doc.text(`Type of Registration.: ${applicationData.registration_type}`, 10, xPos + 75)
  doc.setFontSize(12)
  doc.setFontStyle('bold')
  xPos += 5
  doc.text('Name of Owner', 10, xPos + 80)
  doc.line(10, xPos + 81, doc.internal.pageSize.width - 20, xPos + 81)
  doc.setFontStyle('normal')
  doc.text(`Full name: ${applicationData.last_name}, ${applicationData.first_name} ${applicationData.suffix} ${applicationData.middle_name}`, 10, xPos + 85)
  doc.text(`Address: ${applicationData.address}`, 10, xPos + 90)
  doc.text(`Barangay: ${applicationData.barangay}`, 10, xPos + 95)
  doc.text(`Home Port: ${applicationData.homeport}`, 10, xPos + 100)
  doc.text(`Name of Fishing vessel: ${applicationData.fishing_vessel_name}`, 10, xPos + 105)
  doc.text(`Vessel Type: ${applicationData.fishing_vessel_type}`, 10, xPos + 110)
  doc.text(`Built Place: ${applicationData.built_place}`, 10, xPos + 115)
  doc.text(`Built Year: ${applicationData.built_year}`, 10, xPos + 120)
  doc.text(`Material used: ${applicationData.material_used}`, 10, xPos + 125)
  xPos += 5
  doc.setFontSize(12)
  doc.setFontStyle('bold')
  doc.text('Fishing Vessel Dimensions and Tonnage', 10, xPos + 130)
  doc.line(10, xPos + 131, doc.internal.pageSize.width - 20, xPos + 131)
  doc.setFontStyle('normal')
  doc.text(`Length (meters): ${applicationData.fishing_vessel_dimension_length}`, 10, xPos + 135)
  doc.text(`Breadth (meters): ${applicationData.fishing_vessel_dimension_breadth}`, 10, xPos + 140)
  doc.text(`Depth: ${applicationData.fishing_vessel_dimension_depth}`, 10, xPos + 145)
  doc.text(`Gross tonnage: ${applicationData.fishing_vessel_dimension_gross_tonnage}`, 10, xPos + 150)
  doc.text(`Net tonnage: ${applicationData.fishing_vessel_dimension_net_tonnage}`, 10, xPos + 155)
  xPos += 5
  doc.setFontSize(12)
  doc.setFontStyle('bold')
  doc.text('Particular of propulsion system', 10, xPos + 160)
  doc.line(10, xPos + 161, doc.internal.pageSize.width - 20, xPos + 161)
  doc.setFontStyle('normal')
  doc.text(`Engine make: ${applicationData.engine_make}`, 10, xPos + 165)
  doc.text(`Serial number: ${applicationData.serial_number}`, 10, xPos + 170)
  doc.text(`Horse power: ${applicationData.horse_power}`, 10, xPos + 175)
  xPos += 5
  doc.setFontSize(12)
  doc.setFontStyle('bold')
  doc.text('Classification of fishing gear', 10, xPos + 180)
  doc.line(10, xPos + 181, doc.internal.pageSize.width - 20, xPos + 181)
  doc.setFontStyle('normal')
  doc.text(`Hook and Line: ${applicationData.classification_fishing_gear_hook_and_line_id}`, 10, xPos + 185)
  doc.text(`Pots and Traps: ${applicationData.classification_fishing_gear_pots_traps_id}`, 10, xPos + 190)
  doc.text(`Seine Nets: ${applicationData.classification_fishing_gear_seine_nets_id}`, 10, xPos + 195)
  doc.text(`Falling Gear: ${applicationData.classification_fishing_gear_falling_gear_id}`, 10, xPos + 200)
  doc.text(`Gill Nets: ${applicationData.classification_fishing_gear_gill_nets_id}`, 10, xPos + 205)
  doc.text(`Lift Nets: ${applicationData.classification_fishing_gear_lift_nets_id}`, 10, xPos + 210)
  doc.text(`Scoop Nets: ${applicationData.classification_fishing_gear_scoop_nets_id}`, 10, xPos + 215)
  doc.text(`Miscellaneous Fishing Gears: ${applicationData.classification_fishing_gear_misc_fisher_gears_id}`, 10, xPos + 220)
  doc.setProperties({
    title: pdfName
  })

  return { url: doc.output(willReturnPDF ? 'blob' : 'bloburl'), pdfName: pdfName }
}

export function popupCenter(url, title, w, h) {
  const dualScreenLeft = window.screenLeft !== undefined ? window.screenLeft : window.screenX
  const dualScreenTop = window.screenTop !== undefined ? window.screenTop : window.screenY

  const width = window.innerWidth ? window.innerWidth : document.documentElement.clientWidth ? document.documentElement.clientWidth : screen.width
  const height = window.innerHeight ? window.innerHeight : document.documentElement.clientHeight ? document.documentElement.clientHeight : screen.height

  const systemZoom = width / window.screen.availWidth
  const left = (width - w) / 2 / systemZoom + dualScreenLeft
  const top = (height - h) / 2 / systemZoom + dualScreenTop
  const newWindow = window.open(url, title, 'scrollbars=yes, width=' + w / systemZoom + ', height=' + h / systemZoom + ', top=' + top + ', left=' + left)

  if (!newWindow || newWindow.closed || typeof newWindow.closed === 'undefined') {
    Message({
      type: 'error',
      message: 'Pop-ups are blocked. Please check your settings.'
    })
  } else {
    if (window.focus) newWindow.focus()
  }
}
