<script>
import {
  allUsers,
  uploadFile
} from '@/graphql/users'
import {
  unifiedMutation
} from '@/graphql/unified'

export default {
  name: 'Profile',
  data() {
    const client = process.env.VUE_APP_CLIENT
    return {
      formLoading: true,
      centerDialogVisible: false,
      client: client,
      currentUser: JSON.parse(localStorage.getItem('user')),
      query: {
        module: '',
        data: '',
        user_registration_type: '',
        request: ''
      },
      rules: {},
      dataForm: {
        id: undefined,
        academic_year: '',
        client: client,
        user_type: '',
        user_id: '',
        photo: '',
        parent_info_id: '',
        current_address: '',
        mailing_address: '',
        academic_status: '',
        sequence_number: '',
        learners_reference_no: '',
        student_number: '',
        last_name: '',
        first_name: '',
        suffix: '',
        middle_name: '',
        gender: '',
        dob: '',
        complete_program_name: '',
        contact_number: '',
        acad_level: '',
        father_last_name: '',
        father_first_name: '',
        father_middle_name: '',
        mother_last_name: '',
        mother_first_name: '',
        mother_middle_name: '',
        dswd_household_no: '',
        household_per_capital_income: '',
        permanent_address_street_barangay: '',
        permanent_address_town_city_mun: '',
        permanent_address_province: '',
        permanent_address_zipcode: '',
        total_units: '',
        total_assessment: '',
        disability: '',
        applicant_status: '',
        remarks: '',
        age: '',
        temp_password: '',
        email: ''
      },
      titleRequirement: '',
      ImageUrl: '',
      fileList: [],
      requirements: [],
      genderOptions: [
        { id: 1, value: 'Male', label: 'Male' },
        { id: 2, value: 'Female', label: 'Female' }
      ],
      granteesOptions: [
        { id: 1, value: 'grantees', label: 'Grantees' },
        { id: 2, value: 'on going', label: 'On going' },
        { id: 3, value: 'not enrolled', label: 'Not enrolled' },
        { id: 4, value: 'enrolled', label: 'Enrolled' },
        { id: 5, value: 'graduated', label: 'Graduated' }
      ]
    }
  },
  mounted() {
    this.viewData()
  },
  methods: {
    viewImage(row) {
      console.log('row', row)
      this.centerDialogVisible = true
      this.ImageUrl = row.photo
      this.titleRequirement = row.requirement
    },
    submitUpload(row) {
      console.log('row', row)
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          row['photo'] = e.target.result
          row['status'] = 'U'
          const query = {
            data: row
          }
          console.log('query', query)
          uploadFile(query, (response, success) => {
            if (success) {
              console.log('uploadFile response', response)
              this.$message({
                message: response.uploadFile.message,
                dangerouslyUseHTMLString: true,
                type: 'success'
              })
            } else {
              this.$message({
                message: response.uploadFile.message,
                dangerouslyUseHTMLString: true,
                type: 'error'
              })
            }
          })
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    handleRemove(file, fileList) {
      console.log('handleRemove', file, fileList)
    },
    viewData() {
      this.formLoading = true
      this.dataForm.id = this.currentUser.id
      this.dataForm.email = this.currentUser.email
      const query = {
        first: 1,
        page: 1,
        student: this.currentUser.email,
        registration_type: this.currentUser.registration_type,
        client: this.client
      }
      allUsers(query, (response, success) => {
        if (success) {
          this.formLoading = false
          const data = response.allUsers.data
          if (data.length > 0) {
            console.log('data111', data)
            data.forEach(item => {
              this.requirements = item.requirements
              this.dataForm = item.other_info
              this.dataForm.email = item.email
              item['parent_info'].forEach(row => {
                console.log('item[\'member\']', row['member'])
                if (row['member'] === 'father') {
                  this.dataForm.father_last_name = row['last_name'] ?? ''
                  this.dataForm.father_first_name = row['first_name'] ?? ''
                  this.dataForm.father_middle_name = row['middle_name'] ?? ''
                } else {
                  this.dataForm.mother_last_name = row['last_name'] ?? ''
                  this.dataForm.mother_first_name = row['first_name'] ?? ''
                  this.dataForm.mother_middle_name = row['middle_name'] ?? ''
                }
              })
            })
            console.log('this.requirementList', this.requirements)
          } else {
            this.formLoading = false
          }
        } else {
          this.formLoading = false
        }
      })
    },
    previewImage(event) {
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.dataForm.photo = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    handleSubmit() {
      this.formLoading = true
      this.query.module = this.$route.name
      this.query.user_registration_type = this.currentUser.registration_type
      this.query.request = 'update'
      this.query.data = this.dataForm
      unifiedMutation(this.query, (response, success) => {
        if (success) {
          this.$message({
            message: response.unifiedMutation.message,
            dangerouslyUseHTMLString: true,
            type: 'success'
          })
          this.formLoading = false
        } else {
          this.formLoading = false
          this.$message({
            message: response.message,
            dangerouslyUseHTMLString: true,
            type: 'error'
          })
        }
      })
    },
    resetModel() {
      this.dataForm = {
        id: undefined,
        email: '',
        client: '',
        user_type: '',
        photo: '',
        mailing_address: '',
        academic_status: '',
        student_number: '',
        parent_info_id: 0,
        first_name: '',
        last_name: '',
        middle_name: '',
        dob: '',
        age: '',
        gender: '',
        mobile_number: '',
        password: '',
        phone_number: ''
      }
      this.viewData()
    }
  }
}
</script>

